    <br />
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">

      <!-- 已审核 -->
      <template v-if="params.auditor_status == 2">
        <!-- <el-button @click="auditOrUnaudit('ThreeauditorInvalid')" v-if="authority.b_invalid==1">作废</el-button> -->
      </template>

      <!-- 未审核 -->
      <template v-if="params.auditor_status == 1">
        <el-button type="primary" @click="auditOrUnaudit('auidtorPassThreeOutStorage')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="auditOrUnaudit('ThreeauditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>

      <!-- 作废 -->
      <template v-if="[2,3,4].indexOf(params.auditor_status) != -1 || (authority.b_invalid != 1 && authority.b_pass != 1 && authority.b_nopass !=1 )">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
