<template>
  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>报表系统</el-breadcrumb-item>
      <el-breadcrumb-item>财务报表</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">客户往来明细表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>委托代销单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData" class="demo-form-inline">
        <el-form-item label="库存组织：" size="mini">
          {{TableData.company_name}}
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          {{TableData.department_name}}
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          {{TableData.business_time}}
        </el-form-item>
        <el-form-item label="出库类型：" size="mini">
          <p>委托销售</p>
        </el-form-item>
        <el-form-item label="单据号：" size="mini">
          {{TableData.order_sn }}
        </el-form-item>
        <el-form-item label="客户名称：" size="mini">
          {{TableData.customer_name }}
        </el-form-item>
        <el-form-item label="业务员：" size="mini">
          {{TableData.business_name }}
        </el-form-item>
        <el-form-item label="收货人：" size="mini">
          {{TableData.consignee_name }}
        </el-form-item>
        <el-form-item label="收货人手机号：" size="mini">
          {{TableData.consignee_mobile }}
        </el-form-item>
        <el-form-item label="所属区域：" size="mini">
          {{`${TableData.province_name} ${TableData.city_name} ${TableData.area_name}`}}
        </el-form-item>
        <el-form-item label="详细地址：" size="mini">
          {{TableData.address }}
        </el-form-item>
        <el-form-item label="送货需求：" size="mini">
          {{TableData.delivery_demand_name}}
        </el-form-item>

        <el-form-item label="交货方式：" size="mini">
          {{TableData.hand_over_name }}
        </el-form-item>
        <el-form-item label="回款方式：" size="mini">
          <span v-if="TableData.payment_method==1">客户自结</span>
          <span v-else>物流代收</span>
        </el-form-item>
        <el-form-item label="物流公司：" v-if="TableData.payment_method==2" size="mini">
          <span>{{TableData.settlement_customer_name}}</span>
        </el-form-item>

        <el-form-item label="审核状态：" size="mini">
          {{TableData.auditor_status_name}}
        </el-form-item>

        <el-form-item label="备注：" size="mini">
          <el-input type="text" v-model="TableData.remark" suffix-icon="el-icon-edit-outline" :maxlength="300" :disabled="true" class="boxTextarea" style="width: 200px;"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_info" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="index" width="50" label="序号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" min-width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库" width="132">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位" width="120" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="数量" width="120" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="unit_price" label="单价" align="right" width="100">
          <template slot-scope="scope">
            {{scope.row.unit_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" label="小计金额" align="right" width="100">
          <template slot-scope="scope">
            {{scope.row.subtotal_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>

      <div class="refundauditFoot">
        <span>本单使用返利：{{TableData.rebate_price|priceFormat }}元</span>
        <span>本单使用授信：{{TableData.credit_price|priceFormat}}元</span>
        <span>本单结算金额：{{TableData.settlement_price|priceFormat }}元</span>
      </div>

    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">

      <!-- 已审核 -->
      <template v-if="TableData.auditor_status == 2">
        <!-- <el-button @click="auditOrUnaudit('auditorInvalidSalesOrder')" v-if="authority.b_invalid==1">作废</el-button> -->
      </template>

      <!-- 未审核 -->
      <template v-if="TableData.auditor_status == 1">
        <el-button type="primary" @click="auditOrUnaudit('auditorPassSalesOrder')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="auditOrUnaudit('auditorInvalidSalesOrder')" v-if="authority.b_invalid==1">作废</el-button>
      </template>

      <!-- 作废 -->
      <template v-if="TableData.auditor_status == 3 || TableData.auditor_status == 4 || (authority.b_invalid != 1 && authority.b_pass != 1 && authority.b_nopass !=1 )">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "salescommissionSee",
  data() {
    return {
      authority: {},
      TableData: {},
      chaid: false,
      TableData: {
        is_erp_title: ""
      },
      outstorageTypeList: {} //出库类型列表
    };
  },
  computed: {
    ...mapGetters(["customerdetrptSalescommissionSeeGet"])
  },
  methods: {
    ...mapActions(["customerdetrptSalescommissionSeeACT"]),
    goBack() {
      this.customerdetrptSalescommissionSeeACT({
        moduleName: "Index"
      });
    },
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (column.property === "subtotal_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]).toFixed(2);
        }
      });
      return sums;
    },
    auditOrUnaudit(api) {
      //审核和作废
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          order_id: this.TableData.order_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    }
  },

  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$axios({
      method: "get",
      url: this.$api.checkCommission,
      params: {
        order_id: this.customerdetrptSalescommissionSeeGet.order_id
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        this.$set(this, "TableData", data.data);
      }
    });
    this.outstorageTypeList = this.customerdetrptSalescommissionSeeGet.outstorageTypeList; //出库类型列表
  }
};
</script>
 