      </el-breadcrumb-item>
      <el-breadcrumb-item>查看其他出库</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData" class="demo-form-inline">
        <el-form-item label="单据号：" size="mini" prop="supplier_name">
