          {{TableData.delivery_demand_name}}
        </el-form-item>
        <el-form-item label="交货方式：" size="mini">
          {{TableData.hand_over_name}}
        </el-form-item>
        <el-form-item size="mini" label="回款方式：">
          <span v-if="TableData.payment_method==1">客户自结</span>
          <span v-if="TableData.payment_method==2">物流代收</span>
          <span v-if="TableData.payment_method==3">电商结算</span>
        </el-form-item>
        <el-form-item label="意向物流：" size="mini">
