      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_info" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="序号" type="index" width="50" text-align="center">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" width="170"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" width="100">
        </el-table-column>
