          <template slot-scope="scope">
            <el-tag v-for="(item,index) in scope.row.allocation_id_str" :key="index">
              <span v-if="index == item1.allocation_id" v-for="(item1,index2) in allocationList.options" :key="index2">{{item1.allocation_name}}</span>
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="real_num" label="数量" align="right" width="100">
