        <el-table-column :show-overflow-tooltip="true" property="sale_price" label="单价" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.sale_price | priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" label="小计金额" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.subtotal_price | priceFormat}}
          </template>
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>
