    <div class="refundauditFoot">
      <span v-if="TableData.update_user_name!=''">修改人：{{TableData.update_user_name}}</span>
      <span v-else>创建人：{{TableData.add_user_name}}</span>
      <span v-if="TableData.update_time!=0">修改时间：{{TableData.update_time}}</span>
      <span v-else>创建时间：{{TableData.add_time}}</span>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <template v-if="TableData.auditor_status == 2">
        <!-- 已审核 -->
        <el-button @click="audit('otherStockAuditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 未审核 -->
      <template v-if="TableData.auditor_status == 1">
        <el-button type="primary" @click="audit('otherStockAuidtorPass')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('otherStockAuditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.auditor_status == 3 || TableData.auditor_status == 2 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "salesoutstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        goods_info: [] //只有新增的时候需要
      },
      defaultArea: null, //默认的区域名
      defaultAreaId: [], //选中的区域
      allocationList: {
        options: []
      },
      checkPage: ""
    };
  },
  computed: {
    ...mapGetters([
      "customerdetrptSalesoutstorageSeeGet",
      "salesoutstorageSeeSearch_showGet"
    ])
  },
  methods: {
    ...mapActions([
      "customerdetrptSalesoutstorageSeeACT",
      "outstorageauditDefault_show"
    ]),
    goBack() {
      // if (this.checkPage == "outstorageauditPage") {
      //   this.outstorageauditDefault_show({
      //     commont: "outstorageauditDefault"
      //   });
      // } else {
      this.customerdetrptSalesoutstorageSeeACT({
        moduleName: "Index"
      });
      // }
    },
    audit(api) {
      //审核
      this.$axMethod[api]({
        outstock_id: this.TableData.outstock_id
      }).then(({ data }) => {
        if (data.code == 200) {
          this.$message({
            message: data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    initData() {
      //详情数据
      let outstock_id = "";
      if (
        this.salesoutstorageSeeSearch_showGet &&
        this.salesoutstorageSeeSearch_showGet.outstock_id
      ) {
        outstock_id = this.salesoutstorageSeeSearch_showGet.outstock_id;
      } else {
        outstock_id = this.customerdetrptSalesoutstorageSeeGet.outstock_id;
      }
      this.$axMethod
        .otherStockCheckOutstock({
          outstock_id: outstock_id
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.$set(this, "TableData", data.data);
          }
        });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 6) {
            sums[index] = parseFloat(sums[index]);
          } else if (index == 8) {
            sums[index] = parseFloat(sums[index]);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.TableData.outstock_id = this.customerdetrptSalesoutstorageSeeGet.outstock_id;
    if (this.salesoutstorageSeeSearch_showGet) {
      this.checkPage = this.salesoutstorageSeeSearch_showGet.checkPage;
      if (this.salesoutstorageSeeSearch_showGet.outstock_id) this.initData();
    }
    if (this.customerdetrptSalesoutstorageSeeGet.outstock_id) this.initData();

    this.$common.options("getAllocationListOption", this.allocationList);
  }
};
</script>

