<template>
  <div class="seePage">
    <!--  查看销售退货单 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>报表系统</el-breadcrumb-item>
      <el-breadcrumb-item>财务报表</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">客户往来明细表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看销售退货</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData" class="demo-form-inline">
        <el-form-item label="销售组织：">
          {{TableData.company_name}}
        </el-form-item>
        <el-form-item label="来源单号：">
          {{TableData.outstock_sn}}
        </el-form-item>
        <el-form-item label="业务员：">
          {{TableData.business_name}}
        </el-form-item>
        <el-form-item label="部门：">
          {{TableData.department_name}}
        </el-form-item>

        <el-form-item label="单据号：">
          {{TableData.return_sn}}
        </el-form-item>
        <el-form-item label="客户名称：">
          {{TableData.customer_name}}
        </el-form-item>
        <el-form-item label="退货原因：">
          {{TableData.reason_name}}
        </el-form-item>
        <el-form-item label="业务日期：">
          {{TableData.business_time}}
        </el-form-item>
        <el-form-item label="审核状态：">
          {{TableData.auditor_status_name}}
        </el-form-item>
        <el-form-item label="出库类型：">
          {{TableData.outstorage_type_name}}
        </el-form-item>
        <el-form-item label="意向物流：">
          {{TableData.intentional_logistics}}
        </el-form-item>
        <el-form-item label="备注：">
          <el-input size="small" type="text" v-model="TableData.remark" suffix-icon="el-icon-edit-outline" :maxlength="300" :disabled="true" class="boxTextarea" style="width: 200px;"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_info" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="index" width="50" label="序号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" min-width="110">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="outstock_goods_num" label="商品订购数量" align="right" min-width="120">
          <template slot-scope="scope">
            {{scope.row.outstock_goods_num || ''}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="unit_price" label="销售单价" min-width="120" align="right">

        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="true_unit_price" label="折后单价" align="right" min-width="120">
          <template slot-scope="scope">
            {{scope.row.true_unit_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="return_goods_num" label="退货数量" align="right" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="return_subtotal_price" label="退货总金额" align="right" min-width="120">
          <template slot-scope="scope">
            {{scope.row.return_subtotal_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>

      <div class="refundauditFoot">
        <span v-if="TableData.update_user_name!=''">修改人：{{TableData.update_user_name}}</span>
        <span v-else>创建人：{{TableData.add_user_name}}</span>
        <span v-if="TableData.update_time!=0">修改时间：{{TableData.update_time}}</span>
        <span v-else>创建时间：{{TableData.add_time}}</span>
      </div>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <template v-if="TableData.auditor_status == 2">
        <!-- 已审核 -->
        <el-button @click="audit('invalidReturn')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 未审核 -->
      <template v-if="TableData.auditor_status == 1">
        <el-button type="primary" @click="audit('auditorReturn')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('invalidReturn')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.auditor_status == 3 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportSalesReturnSee",
  data() {
    return {
      authority: {},
      TableData: {},
      chaid: false
    };
  },
  computed: {
    ...mapGetters(["customerdetrptSalesReturnSeeGet"])
  },
  methods: {
    ...mapActions(["customerdetrptIndexACT"]),
    goBack() {
      this.customerdetrptIndexACT({
        moduleName: "Index"
      });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          return_id: this.TableData.return_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (column.property === "return_goods_num") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
        }

        if (column.property === "return_subtotal_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number((prev + curr).toFixed(2));
          }, 0);
          sums[index] = sums[index].toFixed(2);
        }
      });
      return sums;
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$axios({
      method: "get",
      url: this.$api.checkReturn,
      params: {
        return_id: this.customerdetrptSalesReturnSeeGet.return_id
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        this.$set(this, "TableData", data.data);
      }
    });
    console.log(this.customerdetrptSalesReturnSeeGet);
  }
};
</script>

<style scoped>
.title {
  line-height: 50px;
  border-bottom: 1px solid #dddddd;
}

.el-form-item {
  width: 320px;
}

