<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
        <el-form-item label="财务组织" size="mini" :rules="rules.company_id" clearable prop="company_id">
          <el-select v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" :filter-method="companyListInvok" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini" :rules="rules.customer_id" prop="customer_id">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @change="getCustomerInfo" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审核日期" size="mini">
          <el-date-picker v-model="timeValue" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关联单据号" size="mini">
          <el-input v-model="params.business_sn" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="电商订单号" size="mini">
          <el-input v-model="params.ec_order_sn" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button type="primary" @click="onSearch('filterFormRef')" size="mini">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-dropdown placement="bottom-start" v-if="authority.b_export==1 && listShow">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出本客户</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1,false)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2,false)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dropdown placement="bottom-start" v-if="authority.b_exportall==1 && listShow">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出全部</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1,true)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2,true)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="table_container" v-if="listShow">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" @row-dblclick='seeF(TableData.tableData,$event)' style="width: 100%" :max-height="tableHeight">
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="财务组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" label="部门"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_name" label="业务员"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="accounts_type_name" label="单据类型"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivable_price" label="应收款" align="right">
          <template slot-scope="scope">
            {{scope.row.receivable_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receipt_price" label="收款" align="right">
          <template slot-scope="scope">
            {{scope.row.receipt_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="balance" label="余额" align="right">
          <template slot-scope="scope">
            {{scope.row.balance|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" label="审核日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_sn" width="180" label="单据号">
          <template slot-scope="scope">
            <el-button @click="seeF(scope, scope.row)" type="text" size="small">
              {{scope.row.business_sn}}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="ec_order_sn" label="电商订单号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="abstract" label="摘要"></el-table-column>
      </el-table>
      <div class="table_info">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div class="report_row" v-if="TableData.allList!=undefined">生成时间：{{TableData.allList.time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr" v-if="TableData.allList!=undefined">制表人：{{TableData.allList.name}}</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg report_row">
          <el-col :span="12">
            <div>审核日期起始:{{params.start_time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">审核日期截止:{{params.end_time}}</div>
          </el-col>
        </el-row>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getData,
  getEmployeeListCommon,
  exportFile,
  tableResize, //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customerdetrptIndex",
  data() {
    return {
      authority: {},
      params: {},
      searchParams: {},
      listShow: false,
      timeValue: [],
      exportType: null,
      TableData: {
        desc: {},
        tableData: [],
      },
      accountype: {
        options: [],
      },
      companyList: [],
      goPage: 1, //默认搜索页面
      pageSize: 30,
      loading: false,
      per_page: [30, 50, 100, 500],
      tableHeight: 600,
    };
  },
  computed: {
    ...mapGetters(["customerdetrptIndexcGet", "toObtainImport_Get"]),
  },
  methods: {
    ...mapActions([
      "customerdetrptSalesOutSeeACT",
      "customerdetrptIndex",
      "customerdetrptSalesReturnSeeACT",
      "customerdetrptThreeOrderSeeACT",
      "customerdetrptReceiptmanageSeeACT",
      "customerdetrptSalessettlementSeeACT",
      "customerdetrptThreeoutstorageSeeACT",
      "customerdetrptSalesalLocationSeeACT",
      "customerdetrptSalesoutstorageSeeACT",
      "customerdetrptSalescommissionSeeACT",
      "customerdetrptInstorageSeeACT",
      "toObtainImport", //导出状态
    ]),
    getemployeeFoucs() {
      getEmployeeListCommon(this, this.params); //业务员
    },
    companyChange(val) {
      // 获取部门
      this.params.company_name = this.companyList.filter(
        (obj) => obj.company_id === val
      )[0].company_name;
      this.$set(this.params, "customer_id", "");
      this.$set(this.params, "business_id", "");
    },
    exportOrder(scheduleType, type) {
      //导出
      let parameter = Object.assign({}, this.params, {
        export_type: scheduleType,
      });
      if (type) {
        this.$axMethod.exportCustomerDetailList(parameter).then(({ data }) => {
          if (data.code == 200) {
            let num = this.toObtainImport_Get;
            this.toObtainImport(num + 1);
            this.$message({
              message: "导出操作成功，系统正在处理中，请在导出任务中下载！",
              type: "success",
            });
          }
        });
      } else {
        let arg = {
          params: parameter,
          API: "exportCustomerDetailListUrl",
        };
        exportFile(arg);
      }
    },
    seeF(scope, row) {
      let businessId = row.business_sn.slice(-6); //后六位 单据id
      let businessName = row.business_sn.slice(0, 4);
      let billNum = "";
      let actionName = "";
      switch (businessName) {
        case "XSCK":
          billNum = "outstock_id";
          name = "salesOutSee";
          actionName = "customerdetrptSalesOutSeeACT";
          break;
        case "XSTH":
          name = "salesReturnSee";
          billNum = "return_id";
          actionName = "customerdetrptSalesReturnSeeACT";
          break;
        case "TBDD":
          name = "threeOrderSee";
          billNum = "order_id";
          actionName = "customerdetrptThreeOrderSeeACT";
          break;
        case "KHSK":
          name = "receiptmanageSee";
          billNum = "receipt_id";
          actionName = "customerdetrptReceiptmanageSeeACT";
          break;
        case "WTCK":
          name = "salessettlementSee";
          billNum = "outstock_id";
          actionName = "customerdetrptSalessettlementSeeACT";
          break;
        case "TBCK":
          name = "threeoutstOrageSee";
          billNum = "outstock_id";
          actionName = "customerdetrptThreeoutstorageSeeACT";
          break;
        case "XSDB":
          name = "salesalLocationSee";
          billNum = "transfer_id";
          actionName = "customerdetrptSalesalLocationSeeACT";
          break;
        case "QTCK":
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "customerdetrptSalesoutstorageSeeACT";
          break;
        case "DBCK":
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "customerdetrptSalesoutstorageSeeACT";
          break;
        case "PKCK": // 盘亏出库
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "customerdetrptSalesoutstorageSeeACT";
          break;
        case "DBRK":
          name = "instorageSee";
          billNum = "instorage_id";
          actionName = "customerdetrptInstorageSeeACT";
          break;
        case "QTRK":
          name = "instorageSee";
          billNum = "instorage_id";
          actionName = "customerdetrptInstorageSeeACT";
          break;
        case "WTDS":
          name = "salescommissionSee";
          billNum = "order_id";
          actionName = "customerdetrptSalescommissionSeeACT";
          break;
        default:
          break;
      }
      this[actionName]({
        moduleName: name,
        [billNum]: Number(businessId),
        outstock_sn: row.business_sn,
      });
    },
    change(val) {
      if (Array.isArray(val)) {
        this.params.start_time = val[0];
        this.params.end_time = val[1];
      } else {
        this.timeValue = [];
        this.params.start_time = "";
        this.params.end_time = "";
      }
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.$set(this.searchParams, "params", {});
      this.timeValue = [];
      this.listShow = false;
    },
    onSearch(formName) {
      var vm = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (!this.params.company_id) {
            this.$message({
              message: "请先选择财务组织",
              type: "error",
            });
          } else if (!this.params.customer_id) {
            this.$message({
              message: "请先选择客户名称",
              type: "error",
            });
          } else {
            this.listShow = true;
            if (this.timeValue && this.timeValue.length == 2) {
              this.params.start_time = this.timeValue[0];
              this.params.end_time = this.timeValue[1];
            } else {
              this.params.start_time = this.params.end_time = "";
            }
            Object.assign(this.searchParams.params, this.params, {
              page: 1,
            });
            getData(this.searchParams, 2, tableResize.bind(this));
          }
        }
      });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams, 2);
    },
    // customer() {
    //   getCustomerList(this, this.params);
    // },
    // getCompanyFoucs() {
    //   getInventoryFinanceCompanyOpen(this, {
    //     type: 2
    //   });
    // },
    companyRemote(val) {
      let options = {
        type: 2,
        company_name: val,
      };
      // getInventoryFinanceCompanyOpen(this, options);
    },
    getCompany() {
      let options = {
        type: 2,
        company_name: null,
      };
      // getInventoryFinanceCompanyOpen(this, options);
    },
    getAccountype() {
      this.$common.options("getCurrentAccountListOption", this.accountype); //往来类型
    },
  },
  mounted() {
    this.companyList = [
      {
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name,
      },
    ];
    this.$set(this, "params", {
      company_id: Number(localStorage.company_id),
    });
    // getDepartmentListCommon(this, this.params); //获取部门
    this.$set(this, "searchParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getCustomerDetailList", //接口名
      vm: this, //传递的this
    });

    this.$axios({
      method: "get",
      url: "open/RequestOpen/getRequestOpenList",
      params: {
        code: "customerdetrpt",
      },
    }).then((res) => {
      if (res.data.code == 200) {
        if (res.data.data.length != 0) {
          if (res.data.data.is_role == "Y") {
            localStorage.removeItem("authority");
            let authority = JSON.stringify(res.data.data.role_list);
            localStorage.setItem("authority", authority);
            this.authority = JSON.parse(localStorage.getItem("authority"));

            //取消单据右侧滚动条
            tableResize.bind(this)();
            window.onresize = tableResize.bind(this);
          }
        }
      }
    });
  },
};
</script>

